.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

